<template>
  <div class="statisticalChart">
    <el-alert title="当前数据每日更新一次；每日数据有0-$20波动精度偏差，不便之处敬请谅解；撤资请提前一个星期联系作者本人"
              type="info"
              show-icon
              :closable="false">
    </el-alert>
    <div class="view"
         ref="totalTrend"></div>
    <div class="view"
         ref="investorProfit"></div>
    <div class="view"
         ref="userNumbel"></div>
  </div>
</template>
<script>
var echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/bar");
require("echarts/lib/chart/line");
require("echarts/lib/chart/pie");
// 引入提示框和标题组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");
// require("echarts/lib/component/dataRange");
export default {
  data() {
    return {
      financeByUserData: [],
      financeData: []
    };
  },
  created() {
    const _this = this;
    this.getFinanceByUserData()
      .then(res => {
        _this.financeByUserData = res;
        _this.pushTotalTrend();
        _this.pushUserNumbel();
        _this.pushInvestorProfit();
      })
      .catch(err => {
        console.log(err);
      });
  },
  mounted() {
    //挂载结束
  },
  methods: {
    getFinanceData() {
      this.$axios("./static/finance.json").then(msg => {
        this.financeData = msg.data;
      });
    },
    getFinanceByUserData() {
      return new Promise((resolve, reject) => {
        this.$axios("./static/financeByUser.json")
          .then(res => {
            resolve(res.data);
          })
          .catch(err => {
            reject(err);
          });
      });
    },
    pushTotalTrend() {
      const datas = this.financeByUserData.totalTrend;
      let axisDate = [],
        axisValue = [];
      for (let i = 0; i < datas.length; i++) {
        var rows = datas[i];
        axisDate.push(rows.date);
        axisValue.push(rows.total);
      }
      const myChart = echarts.init(this.$refs.totalTrend);
      // 绘制图表
      let option = {
        title: {
          text: "投资总值趋势图"
          // subtext: "每日不定时更新一次"
        },
        tooltip: {
          trigger: "axis",
          formatter: function(params) {
            return params[0].axisValue + "<br/>总值 : $" + params[0].value;
          }
        },
        grid: {
          x: 50,
          x2: 40
        },
        toolbox: {
          show: true,
          feature: {
            mark: { show: true },
            dataView: { show: true, readOnly: false },
            magicType: { show: true, type: ["line", "bar"] },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        calculable: true,
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: axisDate
          }
        ],
        yAxis: [
          {
            type: "value",
            min: 3000,
            max: 9000,
            axisLabel: {
              formatter: "${value}"
            }
          }
        ],
        series: [
          {
            name: "总值",
            type: "line",
            data: axisValue,
            markLine: {
              data: [{ type: "average", name: "平均值" }]
            }
          }
        ]
      };
      myChart.setOption(option, true);
    },
    pushUserNumbel() {
      const datas = this.financeByUserData.userBasic;
      let pieDatas = [];
      for (let i = 0; i < datas.length; i++) {
        var rows = datas[i];
        pieDatas.push({ name: rows.asName, value: rows.quotient });
      }
      const myChart = echarts.init(this.$refs.userNumbel);
      let option = {
        title: {
          text: "总:55份",
          x: "center",
          y: "50%",
          itemGap: 20,
          textStyle: {
            color: "rgba(30,144,255,0.8)",
            fontFamily: "微软雅黑",
            fontSize: 18,
            fontWeight: "bolder"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c}份 ({d}%)"
        },
        series: [
          {
            name: "份额占比",
            type: "pie",
            center: ["50%", "55%"],
            radius: ["30%", "65%"],
            x: "20%", // for funnel
            data: pieDatas,
            itemStyle: {
              // normal: {
              //   label: {
              //     show: true,
              //     position: "center",
              //     formatter: "{b}",
              //     textStyle: {
              //       baseline: "bottom"
              //     }
              //   },
              //   labelLine: {
              //     show: false
              //   }
              // },
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      myChart.setOption(option, true);
    },
    pushInvestorProfit() {
      const datas = this.financeByUserData.userBasic;
      let userData = [],
        valuesData = [];
      for (let i = 0; i < datas.length; i++) {
        var rows = datas[i];
        userData.push(rows.asName);
        valuesData.push(rows.startTotal);
      }
      const myChart = echarts.init(this.$refs.investorProfit);
      let option = {
        title: {
          text: "投资利润",
          subtext: "已扣除手续费"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          },
          formatter: function(params) {
            return (
              params[0].name +
              "<br/>盈利 : $" +
              params[1].value +
              "<br/>" +
              params[1].seriesName +
              " : $" +
              (params[1].value + params[0].value) +
              "<br/>" +
              params[0].seriesName +
              " : $" +
              params[0].value
            );
          }
        },
        legend: {
          selectedMode: false,
          data: ["投资", "总值"]
        },
        toolbox: {
          show: true,
          feature: {
            mark: { show: true },
            dataView: { show: true, readOnly: false },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        calculable: true,
        grid: {
          x: 50,
          x2: 40
        },
        xAxis: [
          {
            type: "category",
            data: userData
          }
        ],
        yAxis: [
          {
            type: "value",
            boundaryGap: [0, 0.1],
            // min: 3000,
            // max: 9000,
            axisLabel: {
              formatter: "${value}"
            }
          }
        ],
        series: [
          {
            name: "投资",
            type: "bar",
            stack: "sum",
            barCategoryGap: "50%",
            itemStyle: {
              normal: {
                color: "tomato",
                barBorderColor: "tomato",
                barBorderWidth: 6,
                barBorderRadius: 0,
                label: {
                  show: true,
                  position: "insideTop"
                }
              }
            },
            data: [3000, 300, 500, 200, 500, 1000]
          },
          {
            name: "总值",
            type: "bar",
            stack: "sum",
            itemStyle: {
              normal: {
                color: "#fff",
                barBorderColor: "tomato",
                barBorderWidth: 6,
                barBorderRadius: 0,
                label: {
                  show: true,
                  position: "top",
                  formatter: function(params) {
                    for (
                      var i = 0, l = option.xAxis[0].data.length;
                      i < l;
                      i++
                    ) {
                      if (option.xAxis[0].data[i] == params.name) {
                        return option.series[0].data[i] + params.value;
                      }
                    }
                  },
                  textStyle: {
                    color: "tomato"
                  }
                }
              }
            },
            data: [600, 60, 100, 40, 100, 200]
          }
        ]
      };
      myChart.setOption(option);
    }
  }
};
</script>
<style scoped>
.statisticalChart {
  width: 100%;
  overflow: auto;
}
.statisticalChart div.view {
  height: 300px;
  margin: 20px 0;
  border-radius: 3px;
  background-color: #fff;
  border: 1px solid #e3e3e3;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
}
</style>




